.tada-animation {
  -webkit-animation-name: tada;
          animation-name: tada;
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.hover-animation {
  -webkit-animation-name: hover;
          animation-name: hover;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.animation-delay-1s {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

/* Modified from animate.css */
@-webkit-keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  16.67% {
    -webkit-transform: scale3d(0.85, 0.85, 0.85) rotate3d(0, 0, 1, -4deg);
            transform: scale3d(0.85, 0.85, 0.85) rotate3d(0, 0, 1, -4deg);
  }

  33.67%,
  66.67% {
    -webkit-transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, -4deg);
            transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, -4deg);
  }

  50%,
  83.67% {
    -webkit-transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, 4deg);
            transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, 4deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}
@keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  16.67% {
    -webkit-transform: scale3d(0.85, 0.85, 0.85) rotate3d(0, 0, 1, -4deg);
            transform: scale3d(0.85, 0.85, 0.85) rotate3d(0, 0, 1, -4deg);
  }

  33.67%,
  66.67% {
    -webkit-transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, -4deg);
            transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, -4deg);
  }

  50%,
  83.67% {
    -webkit-transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, 4deg);
            transform: scale3d(1.15, 1.15, 1.15) rotate3d(0, 0, 1, 4deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@-webkit-keyframes hover {
  from,
  2% {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }

  48%,
  52% {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }

  98%,
  to {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }
}

@keyframes hover {
  from,
  2% {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }

  48%,
  52% {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }

  98%,
  to {
    -webkit-transform: translateY(-0.6rem);
            transform: translateY(-0.6rem);
  }
}



